import React,{ Fragment, useEffect } from "react"
import "./introduction.less"
import "./style.css"
import {start,destoryOn} from "./3dWorld/start"

function Intro(){
    useEffect(() => {
        start()
        return destory
    })
    const destory = () =>{
        destoryOn()
    }
    return (
        <Fragment>
            <div className={"luy-out"}>
                <div className={"mask-load"} id="loading">
                    <div class="trinity-rings-spinner preload">
                        <div class="circle preload"></div>
                        <div class="circle preload"></div>
                        <div class="circle preload"></div>
                    </div>
                    <div class="loading-text-div preload">Loading<span class="loader__dot">.</span><span class="loader__dot">.</span><span class="loader__dot">.</span></div>
                </div>
                <div id="intro"></div>
            </div>
        </Fragment>
    )
}
export default Intro